export default class BackendConnector {
    constructor(base_url) {
        this.base_url = base_url;
    }
    async get_music_list() {
        const response = await fetch(`${this.base_url}/musics`);
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return response.json();
    }
    async get_music_by_id(id) {
        const response = await fetch(`${this.base_url}/musics/${id}`);
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return response.json();
    }
    async play_note(port, note) {
        const response = await fetch(`${this.base_url}/notes?n=${note}&p=${port}`);
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return;
    }
    async stop_note(ports) {
        const response = await fetch(`${this.base_url}/notes?${ports.map((it) => `p=${it}`).join('&')}`, { method: 'DELETE' });
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return;
    }
    async stop_all_notes() {
        const response = await fetch(`${this.base_url}/notes`, {
            method: 'DELETE',
        });
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return;
    }
    async get_status() {
        const response = await fetch(`${this.base_url}/status`);
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return response.json();
    }
    async get_playing_music() {
        const response = await fetch(`${this.base_url}/status/play`);
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
        return response.json();
    }
    async post_playing_music(params) {
        const response = await fetch(`${this.base_url}/status/play`, {
            method: 'POST',
            body: JSON.stringify(params),
            headers: [['Content-Type', 'application/json']],
        });
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
    }
    async stop_playing_music() {
        const response = await fetch(`${this.base_url}/status/play`, {
            method: 'DELETE',
        });
        if (!response.ok) {
            throw new Error(`Failed to invoke api: ${response.statusText}`);
        }
    }
}
